export default class CalendarController {
	constructor() {
		'ngInject';
		this.type = 'range';                                       //
		this.model = '2016-10-30 12:02:00';                         //
		this.startModel = '2016-10-30 13:12:00';              //
		this.placeholders = ['', ''];          //placeholders
		this.separator = '';                                      //
		this.startDisabled = true;                                 //
		this.minTime = '2016-09-09 09:09:00';                                  //
		this.maxTime = '2018-09-09 09:09:00';                                 //
		this.startView = 'month';                                   //
		this.minView = 'month';                                     //
		this.startDisabled = true;
		this.minuteStep = 10;
	}
}


