export default class PopconfirmController {
	constructor($scope, Popconfirm) {
		'ngInject';
		this.$scope = $scope;
		this.Popconfirm = Popconfirm;
	}

	topPop(e) {
		this.Popconfirm.pop({
			title: 'top?',
			placement: 'top',
			okText: '',
			cancelText: ''
		}, e).then(function () {
			alert('');
		}, function () {
			alert('');
		});
	}

	leftPop(e) {
		this.Popconfirm.pop({
			title: 'left?',
			placement: 'left',
			okText: '',
			cancelText: ''
		}, e).then(function () {
			alert('');
		}, function () {
			alert('');
		});
	}

	bottomPop(e) {
		this.Popconfirm.pop({
			title: 'bottom?',
			placement: 'bottom',
			okText: '',
			cancelText: ''
		}, e).then(function () {
			alert('');
		}, function () {
			alert('');
		});
	}

	rightPop(e) {
		this.Popconfirm.pop({
			title: 'right?',
			placement: 'right',
			okText: '',
			cancelText: ''
		}, e).then(function () {
			alert('');
		}, function () {
			alert('');
		});
	}
}


