import listComponent from './list.component';
import bpAjax from 'bp-ajax';
import {listTpl} from 'fancyui';

export default angular.module('list', [
		bpAjax.name,
		listTpl.name
	])
	/**
	 * routerConfig.js
	 */
	.component('list', listComponent);



